
.top {
  height: 60px;
  background-color: #416eb6;
}
.top .search {
    width: 80%;
    margin: 0 auto;
    padding-top: 13px;
    position: relative;
    height: 100%;
}
.top .search input {
      height: 30px;
      border-radius: 5px;
      width: 100%;
      opacity: .1;
}
.top .search .inputMask {
      position: absolute;
      top: 0;
      height: 30px;
      border-radius: 5px;
      width: 100%;
      text-align: center;
      color: white;
}
.home-content {
  min-height: 250px;
}
.home-content .cells {
    margin: 0;
}
.home-content .cells .row {
      border: none;
}
.home-content .cells .row .hairline-left:before, .home-content .cells .row .hairline-top:before {
        width: 0;
        height: 0;
}
.home-content .cells .row .tool .icon {
        height: 30px;
        font-size: 30px;
        line-height: 50px;
}
.home-content .cells .row .tool span {
        font-size: 12px;
}
.home-content .tools-content {
    height: 100px;
}
.home-content .tools-content .cells {
      height: 100%;
      background-color: #416eb6;
}
.home-content .tools-content .cells .row {
        height: 100px;
        color: white;
}
.home-content .tools-content .cells .row .col {
          height: 100px;
}
.home-content .modules {
    height: 150px;
}
.home-content .modules .row {
      height: 75px;
      color: #416eb6;
}
.home-content .modules .row .col {
        height: 75px;
}
.home-content .modules .row .col i {
          font-size: 24px !important;
}
.news-list {
  width: 100%;
  margin-bottom: 50px;
}
.news-list .news {
    height: 120px;
    padding: 15px;
    display: -ms-flexbox;
    display: flex;
}
.news-list .news img {
      float: left;
}
.news-list .news .news-content {
      height: 100%;
      margin-left: 15px;
      width: 60%;
}
.news-list .news .news-content .title {
        width: 100%;
        font-weight: bold;
}
.news-list .news .news-content .detail {
        margin-top: 10px;
        font-size: 12px;
        opacity: .5;
}
#banner {
  width: 100%;
  height: 200px;
  overflow-x: scroll;
}
#banner::-webkit-scrollbar {
    width: 0;
    height: 0;
}
#banner #bannerBox {
    height: 100%;
    box-sizing: content-box;
    padding: 0 8%;
    font-size: 0;
}
#banner #bannerBox :after {
      display: block;
      content: " ";
      clear: both;
}
#banner #bannerBox .bannerItem {
      height: 100%;
      display: inline-block;
}
#banner #bannerBox .bannerItem .itemContent {
        width: 100%;
        height: 100%;
        background: #416eb6;
        font-size: 20px;
        transform: scale(1, 1);
        transition: all .1s;
}



