
.mats .req-item {
  width: 100%;
  height: 100%;
  color: #ababab;
  position: relative;
}
.mats .req-item .req-item-title {
    line-height: 30px;
    font-size: 16px;
}
.mats .req-item .req-item-title .icon {
      float: right;
      font-size: 24px;
}
.mats .req-item .req-item-content {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.mats .req-item .req-item-content span {
      -ms-flex: 1 0 50%;
          flex: 1 0 50%;
      line-height: 26px;
}
.bom-swipe-item {
  width: 125%;
  height: 128px;
  position: relative;
  transition: all .3s;
  left: 0%;
}
.bom-swipe-item:after {
    display: block;
    content: " ";
    width: 99%;
    height: 1px;
    background: #ccc;
    position: absolute;
    bottom: 0;
    right: 0;
}
.bom-swipe-item:last-child:after {
    display: none;
}
.bom-swipe-item .swipe-content {
    width: 80%;
    height: 128px;
    padding: 15px;
    background: #fff;
    position: relative;
    float: left;
    color: #ababab;
}
.bom-swipe-item .swipe-content > div {
      width: 90%;
}
.bom-swipe-item .swipe-content > div h3 {
        font-size: 16px;
        height: 20px;
        line-height: 20px;
        margin-bottom: 8px;
        color: #ababab;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        overflow: hidden;
}
.bom-swipe-item .swipe-content > div h3 span {
          color: #416eb6;
}
.bom-swipe-item .swipe-content > div p {
        font-size: 14px;
        line-height: 14px;
}
.bom-swipe-item .swipe-content > div p span {
          display: inline-block;
          margin-right: 10px;
}
.bom-swipe-item .swipe-content > .icon {
      display: block;
      width: 13%;
      height: 128px;
      font-size: 35px;
      line-height: 128px;
      position: absolute;
      right: 0px;
      top: 0;
      background: #fff;
}
.bom-swipe-item .swipe-content > .icon:before {
        margin-left: 12px;
}
.bom-swipe-item .swipe-content > .right {
      display: block;
      width: 18%;
      height: 128px;
      font-size: 14px;
      line-height: 128px;
      position: absolute;
      right: 0px;
      color: #416eb6;
      top: 0;
      background: #fff;
}
.bom-swipe-item .swipe-click {
    width: 80%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 10;
    display: none;
}
.bom-swipe-item.active {
    left: -25%;
}
.bom-swipe-item.active .swipe-click {
      display: block;
}
.bom-swipe-item .swipe-side {
    width: 20%;
    height: 100%;
    line-height: 128px;
    background: #FE3B32;
    float: left;
    color: #fff;
    position: relative;
    text-align: center;
}
.select-box {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  background: transparent;
  z-index: -100;
}
.select-box .select-click {
    width: 19.5%;
    height: 100%;
    float: left;
}
.select-box .select-main {
    width: 80%;
    height: 100%;
    background: #F3F6FB;
    position: absolute;
    transition: all .3s linear;
    top: 0;
    right: -100%;
}
.select-box .select-main .main-header {
      height: 80px;
      width: 100%;
      position: relative;
      z-index: 20;
}
.select-box .select-main .main-header p {
        height: 40px;
        line-height: 40px;
        text-align: center;
        margin: 0;
}
.select-box .select-main .main-header input {
        background-color: #bbbbbb;
        width: 90%;
        margin: 0px auto;
        border-radius: 5px;
}
.select-box .select-main .main-list {
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 10;
      box-sizing: border-box;
      padding-top: 80px;
      overflow-y: scroll;
}
.select-box .select-main .main-list::-webkit-scrollbar {
        width: 0;
        height: 0;
}
.select-box .select-main .main-list p {
        height: 50px;
        line-height: 50px;
        border-bottom: 1px solid #ccc;
        padding-left: 20px;
}
.select-box.active {
    z-index: 100;
    background: rgba(0, 0, 0, 0.3);
}
.select-box.active .select-main {
      right: 0%;
}



